import React from "react"
import { Link } from "gatsby"
import Layout from "../components/layout"
import Image from "../components/image"
import SEO from "../components/seo"

const FlabPage = () => (
    <Layout>
    <SEO title="FLAB" />
    <Link to="/">메인 페이지로</Link>
    <h1>FLAB</h1>
    <p>FLAB 관련 정보 페이지</p>
    
    <h2>FreeXanta (2020.01~2020.03)</h2>
    <p>제목 : 안드로이드 용의 프리젠터 앱 개발</p>
    <ul>
        <li>앱 개발과 출시에 대한 경험 축적</li>
        <li>PC와 상호작용하는 안드로이드 앱의 개발 경험 축적</li>
        <li>안드로이드 앱 개발 방법론에 대한 경험</li>
    </ul>
    <p>
        관련 문서
        <ul>
            <li>
                <a href="https://docs.google.com/spreadsheets/d/1FDQcwQtG6Zj0wsgKWcrQvouCfXOFV6ou3SBBAvffn5M/edit?usp=sharing">FreeXanta 사양 정의</a>
            </li>
        </ul>
    </p>
    <p>깃허브 저장소
        <ul>
            <li>FreeXanta PC측 프로그램 : https://github.com/trkangcc/FreeXantaPC</li>
            <li>FreeXanta 안드로이드 클라이언트 : https://github.com/trkangcc/FreeXantaAndroidClient</li>
        </ul>
    </p>

    </Layout>
)

export default FlabPage